/* eslint-disable no-unused-vars */ /* eslint-disable camelcase */ /*
eslint-disable camelcase */ /* eslint-disable camelcase */
<template>
  <div class="login-container">
    <el-form ref="form" :model="form" label-width="20%" class="login-form">
      <div class="login-title">DISC性格测试</div>
      <el-form-item label="手机号">
        <el-input v-model="form.username"></el-input>
      </el-form-item>
      <el-form-item label="验证码">
        <el-input style="width:40%" v-model="form.password"></el-input>
        <el-button style="margin-left:10px" type="primary" :disabled="form.gCode" @click="getCode">{{
            form.gCode ? form.codeCountDown + "s后重新发送" : "获取验证码"
          }}</el-button>
      </el-form-item>
      <el-button type="primary" @click="onSubmit">登录</el-button>
    </el-form>
  </div>
</template>

<script>
import { mapMutations } from 'vuex'

export default {
  name: 'login',
  props: {
    msg: String
  },
  data () {
    return {
      form: {
        username: '',
        password: '',
        gCode: false,
        codeCountDown: 0
      }
    }
  },
  methods: {
    ...mapMutations(['changeLogin']),

    onSubmit () {
      // 登录
      this.axios({
        method: 'post',
        url: 'Https://character.wdmedu.com:44444/user/cverify',
        data: {
          mobile: this.form.username,
          verifycode: this.form.password
        }
      }).then(res => {
        if (res) {
          if (res.data.code === 20000) {
            this.$message({
              message: '登录成功',
              type: 'success'
            })
            // eslint-disable-next-line no-undef
            this.changeLogin({ Authorization: 'token' })
            this.$router.push({ path: '/test' })
          } else {
            this.$message.error(res.data.message)
          }
        }
      })
    },

    // 获取验证码
    getCode () {
      this.axios({
        method: 'post',
        url: 'Https://character.wdmedu.com:44444/user/mverify',
        data: {
          mobile: this.form.username
        }
      }).then(res => {
        if (res) {
          if (res.data.code === 20000) {
            this.form.gCode = true
            this.form.codeCountDown = 60
            // eslint-disable-next-line no-unused-vars
            const clock = window.setInterval(() => {
              this.form.codeCountDown--
              if (this.form.codeCountDown <= 0) {
                this.form.gCode = false
                clearInterval(clock)
              }
            }, 1000)
          } else {
            this.$message.error(res.data.message)
          }
        }
      })
    }
  }
}
</script>

