import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '../views/Home.vue'

Vue.use(VueRouter)

const routes = [{
  path: '/',
  name: 'Home',
  component: Home,
  meta: {
    title: '登录', // 标题设置,
    type: '' // 不需要鉴权
  }
},
{
  path: '/test',
  name: 'Test',
  component: () => import('../views/test.vue'),
  meta: {
    title: '性格测试', // 标题设置
    type: 'login' // 是否需要判断是否登录,这里是需要判断
  }
}
]

const router = new VueRouter({
  // mode: 'history',
  base: process.env.BASE_URL,
  routes
})

export default router
